.main-about {
  width: 100vw;
  margin-top: 5vh;
  gap: $gap-unit;
  @include flex(center);
  &__img {
    width: 30vw;
  }
  &__text {
    width: 50vw;
    h3 {
      letter-spacing: 10px;
      border-bottom: 4px solid saddlebrown;
    }
    p {
      line-height: 25px;
    }
  }
}
