@media screen and (max-width: 756px) {
  img {
    width: 100% !important;
  }
  p {
    padding: 0 0.5em;
  }
  .open {
    display: block;
    position: absolute;
    right: 25px;
    font-size: 2.5rem;
  }
  .main-header {
    &__logo {
      position: absolute;
      left: 0;
    }
    &__list {
      position: fixed;
      top: 0px;
      right: 0;
      width: 100%;
      text-align: center;
      height: 50%;
      padding: 2vh 0;
      background-color: aliceblue;
      &__navbar {
        display: inline-block;
        flex-direction: column;
        li {
          margin: 5vh 0;
        }
      }
    }
  }

  .main-header__icon {
    display: none;
  }
  .main-about {
    flex-direction: column;

    &__img {
      width: 100%;
    }
    &__text {
      h3 {
        font-size: 1.5rem;
        letter-spacing: 0;
      }
      width: 100%;
    }
  }

  .main-contact {
    flex-direction: column;
    &__img {
      width: 100% !important;
    }
    &__form {
      display: grid;
      position: relative;
      gap: 25px;

      input,
      textarea {
        width: 350px;
      }

      textarea {
        max-width: 350px;
        min-width: 350px;
      }
    }
  }
  .footer-menu {
    display: none;
  }
  .main-home {
    height: 100vh;
    h3,
    h2 {
      font-size: 1.5rem !important;
      letter-spacing: 0;
    }
    &__main {
      flex-direction: column-reverse;
      &__img {
        width: 100%;
        img {
          border-radius: 0px;
        }
      }
      &__text {
        width: 100%;
      }
    }
  }
  .main-adv {
    h3,
    h2 {
      font-size: 1.5rem !important;
      letter-spacing: 0;
    }
  }
  .main-what {
    margin-bottom: 5vh;
    h3,
    h2 {
      font-size: 1.5rem !important;
      letter-spacing: 0;
    }
    .main-what {
      height: 100%;

      flex-direction: column;
      h3,
      h2 {
        font-size: 1.5rem !important;
        letter-spacing: 0;
      }
      &__img {
        img {
          width: 90vw !important;
        }
      }
      &__text {
        p {
          width: 100vw;
        }
      }
    }
  }

  .main-popular {
    display: none;
  }
  .main-libary__search__input {
    width: 80vw;
  }
  .main-libary__list__item__items {
    grid-template-columns: auto;
    gap: 25px;
  }
  .main-libary__list__item__items__div {
    width: 370px;
  }
}
